.app__works {
    flex: 1;
    width: 100%;
    flex-direction: column;

}

.app__work-filter {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;

    margin: 4rem 0 2rem;

    .app__work-filter-item {
        padding: 0.5rem 1rem;
        border-radius: 5rem;
        background-color: var(--gray-color);
        color: #000;
        font-weight: 800;
        cursor: pointer;
        transition: all 0.3s ease;
        margin: 0.5rem;

        &:hover {
            background-color: var(--secondary-color);
            color: #fff;
        }

        @media screen and (min-width: 2000px) {
            padding: 1rem 2rem;
            border-radius: 5.5rem;
        }
    }

    .item-active {
        background-color: var(--secondary-color);
        color: #fff;
    }
}

.app__work-portfolio {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    .app__work-item {
        width: 270px;
        flex-direction: column;
        margin: 2rem;
        border-radius: 0.5rem;
        padding: 1rem;
        background-color: var(--darkGray-color);
        color: #000;
    
        cursor: pointer;
        transition: all 0.3s ease;
    
        &:hover{
            box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
        }
    
        @media screen and (min-width: 2000px) {
            width: 470px;
            padding: 1.25rem;
            border-radius: 0.75rem;
        }
        @media screen and (max-width: 300px) {
            width: 100%;
            margin: 1rem;
        }
    }

   
}


.app__work-img {
    width: 100%;
    height: 230px;
    position: relative;

    img {
        width: 100%;
        height: 100%;
        border-radius: 0.5rem;
        object-fit: cover;
    }

    @media screen and (min-width: 2000px) {
        height: 350px;
    }
}

.app__work-hover {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease-in-out;
    border-radius: 0.5rem;
    opacity: 0; // Initially hide the hover content
    cursor: pointer;
    
    a {
        opacity: 0;
    }

    &:hover {
        opacity: 1; // Show the hover content on hover
        background-color: rgba(0, 0, 0, 0.3); // Move the background color here to ensure it covers the whole area
        a {
            opacity: 1;
        }
    }

    div {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: rgba(0, 0, 0, 0.5);
        color: #fff;
        margin: 1rem;
        font-family: var(--font-base);
        cursor: pointer;
        transition: all 0.3s ease;

        svg {
            width: 50%;
            height: 50%;
            color: white;
        }
    }
}

.app__work-content {
    padding: 0.5rem;
    width: 100%;
    position: relative;
    flex-direction: column;

    h4 {
        margin-top: 1rem;
        line-height: 1.5;
    }

    .app__work-tag {
        position: absolute;
        padding: 0.5rem 1rem;

        border-radius: 10px;
        background-color: var(--darkGray-color);
        top: -25px;
        p{
            color: #fff
        }
    }

}